import React, {useState} from "react";

import searchIcon from '../assets/desktop/icon-search.svg'
import locationIcon from '../assets/desktop/icon-location.svg'
import checkIcon from '../assets/desktop/icon-check.svg'
import searchIconWhite from '../assets/desktop/icon-search-white.svg'
import filterIcon from '../assets/mobile/icon-filter.svg'
import closeIcon from '../assets/mobile/close.svg'

import './styles/SearchBar.scss'



const SearchBar = ({handleSearchClick}) => {
    const [searchParams, setSearchParams] = useState({
        description: "",
        location: "",
        full_time: false
    })
    const [windowWidth, setWindowWidth] = useState(window.innerWidth)
    const [modalOpen, setModalOpen] = useState(false)

    window.addEventListener('resize', () => setWindowWidth(window.innerWidth))

    const handleInput = e => {
       setSearchParams({
           ...searchParams,
           [e.target.name]: e.target.value,
       })
    }

    const handleModalOpen = () => {
        setModalOpen(!modalOpen)
    }

    const handleCheckbox = () => {
        setSearchParams({
            ...searchParams,
            full_time: !searchParams.full_time,
        })
    }


    return (
        <div className="searchBar" >
            <div className="wrapper">
                <div className="searchBar__container">
                    <div className="searchBar__title">
                        {windowWidth > 768 && (<img src={searchIcon} alt="search"/>)}
                        <input
                            type="text"
                            name="description"
                            id="title--input"
                            placeholder={windowWidth < 992? "Filter by title...":"Filter by title, companies, expertise..." }
                            onChange={handleInput}
                        />
                    </div>
                    {windowWidth >= 768 && (
                        <>
                            <div className="searchBar__location">
                                <img src={locationIcon} alt="location"/>
                                <input
                                    type="text"
                                    name="location"
                                    placeholder="Filter by location..."
                                    onChange={handleInput}
                                />
                            </div>
                            <div className="searchBar__fullTimeJob">
                                <label className="custom-checkbox">
                                <input
                                type="checkbox"
                                className="custom-checkbox-input"
                                name="fullTimeJob"
                                onChange={handleCheckbox}
                                />
                                <span
                                className="custom-checkbox-show custom-checkbox-show--checkbox"
                                style={searchParams.full_time?{backgroundImage: `url(${checkIcon})`}:null}
                                />
                                <span className="custom-checkbox-text">{windowWidth <= 1120 ? "Full Time":"Full Time Only"}</span>
                                </label>
                            </div>
                        </>
                    )
                    }
                    {
                        windowWidth < 768 && (
                            <>
                                <button className="filterButton" onClick={handleModalOpen}>
                                    <img src={filterIcon} alt="filter"/>
                                </button>
                                <div className={`filter__modal ${modalOpen?"active": ""}`} >
                                    <button className="closeModal" onClick={handleModalOpen}>
                                        <img src={closeIcon} alt="close"/>
                                    </button>
                                    <div className="modal__container">
                                        <div className="searchBar__location">
                                            <img src={locationIcon} alt="location"/>
                                            <input
                                                type="text"
                                                name="location"
                                                placeholder="Filter by location..."
                                                onChange={handleInput}
                                            />
                                        </div>
                                        <div className="searchBar__fullTimeJob">
                                            <label className="custom-checkbox">
                                                <input
                                                    type="checkbox"
                                                    className="custom-checkbox-input"
                                                    name="fullTimeJob"
                                                    onChange={handleCheckbox}
                                                />
                                                <span
                                                    className="custom-checkbox-show custom-checkbox-show--checkbox"
                                                    style={searchParams.full_time?{backgroundImage: `url(${checkIcon})`}:null}
                                                />
                                                <span className="custom-checkbox-text">{windowWidth <= 1120 ? "Full Time":"Full Time Only"}</span>
                                            </label>
                                        </div>
                                        <button
                                            className="searchBar__search--button"
                                            onClick={() => {
                                                handleModalOpen()
                                                handleSearchClick(searchParams)
                                            }}>
                                            Search
                                        </button>
                                    </div>
                                </div>
                            </>
                        )
                    }
                    <div className="searchBar__search">
                        <button
                            className="searchBar__search--button"
                            onClick={() => handleSearchClick(searchParams)}
                        >
                            {windowWidth < 768? <img src={searchIconWhite} alt="search"/>:"Search"}
                        </button>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default SearchBar