import React from "react"
import {getTimeAgo} from "../utils/utils";

import './styles/JobDetails.scss'

const JobDetails = ({created_at, type, title, location, description, company_url}) => (
    <div className="jobDetails">
        <header>
            <div className="jobDetails__overview">
                <p className="jobDetails__timeInfo">
                    {getTimeAgo(created_at)}<span> . </span>{type}
                </p>
                <h1 className="jobDetails__title">
                    {title}
                </h1>
                <h4 className="jobDetails__location">
                    {location}
                </h4>
            </div>
            <div className="jobDetails__applyNow">
                <a href={company_url} className="button">Apply Now</a>
            </div>
        </header>
        <div className="jobDetails__description" dangerouslySetInnerHTML={{ __html: description }}/>
    </div>
)

export default JobDetails
