import React, {useState, useEffect} from 'react'

import CompanyInfo from "../components/CompanyInfo";
import JobDetails from "../components/JobDetails";
import HowToApply from "../components/HowToApply";
import JobDetailsFooter from "../components/JobDetailsFooter";

import Loader from "../components/Loader";

import './styles/Details.scss'

const Details = (props) => {
    const [state, setState] = useState({
        loading:true,
        data: []
    })

    const id = props.match.params.id
    
    const fetchData = async (URL) => {
        const response = await fetch(URL)
        const data = await response.json()

        setState({
            loading: false,
            data: data
        })
    }

    useEffect(() => {
        const URL = `https://cors-anywhere.herokuapp.com/https://jobs.github.com/positions/${id}.json`
        fetchData(URL)
    }, [])

    return (
            state.loading ?
                <div className="details">
                    <Loader/>
                </div>
            :
            <>
                <div className="details">
                    <CompanyInfo company_logo={state.data.company_logo} company={state.data.company} company_url={state.data.company_url}/>
                    <JobDetails {...state.data} />
                    <HowToApply how_to_apply={state.data.how_to_apply}/>
                </div>
                <JobDetailsFooter title={state.data.title} company={state.data.company} company_url={state.data.company_url}/>
            </>
    )
}

export default Details