import React from 'react'

import './styles/HowToApply.scss'

import backgroundImg from '../assets/desktop/bg-pattern-detail-footer.svg'

const HowToApply = ({how_to_apply}) => (
    <div className="how_to_apply" style={{backgroundImage: `url(${backgroundImg})`}}>
        <h3>How To Apply</h3>
        <div className="description" dangerouslySetInnerHTML={{__html: how_to_apply}} />
    </div>
)

export default HowToApply

