import React, {useState, useEffect} from "react";
import JobItem from "./JobItem";
import Loader from "./Loader";

import './styles/JobsGrid.scss'

const JobsGrid = (props) => {

    const [state, setState] = useState({
        loading: false,
        page: 1,
        data:[],
        searchParams: props,
    })



    const fetchJobs = async () => {
        setState({
            ...state,
            loading: true,
        })

        const {description, location, full_time} = state.searchParams

        const URLParams = `&location=${location}&description=${description}&full_time=${full_time?"on":""}`
        const URL = `https://cors-anywhere.herokuapp.com/https://jobs.github.com/positions.json?page=${state.page}${URLParams}`

        const response = await fetch(URL)
        const data = await response.json()

        setState({
            ...state,
            page: state.page + 1,
            loading: false,
            data : [].concat(
                state.data,
                data
            )
        })

    }

    useEffect(() => {
        setState({
            ...state,
            page: 1,
            data: [],
            searchParams: props,
        })
    }, [props])

    useEffect(() => {
        fetchJobs()
    }, [state.searchParams])



    return (
        <main className={"jobsGrid"}>
            <div className="wrapper">
                <div className="jobsGrid__container">
                    {state.data.map(job => {
                        return (
                            <JobItem {...job} key={job.id}/>
                        )
                    })
                    }
                </div>
                {state.loading && (
                    <Loader/>
                )}
                {!state.loading && (
                    <div className="jobsGrid__loadMore">
                        <button
                            className="jobsGrid__loadMore--button"
                            onClick={() => fetchJobs()}
                        >
                            Load more
                        </button>
                    </div>
                )}
            </div>
        </main>
    )
}

export default JobsGrid