import React from "react";

import './styles/JobDetailsFooter.scss'

const JobDetailsFooter = ({title, company, company_url}) => (
    <footer>
        <div className="wrapper">
            <div className="jobInfo">
                <h3>{title}</h3>
                <p>{company}</p>
            </div>
            <div className="applyNow">
                <a href={company_url} className="button">Apply Now</a>
            </div>
        </div>
    </footer>
)

export default JobDetailsFooter