import React from "react"

import './styles/CompanyInfo.scss'

const CompanyInfo = ({company_logo, company, company_url }) => {
    const companyUrlArr = company_url.split('.')
    companyUrlArr.shift()
    const companyUrlShort = companyUrlArr.join('.').replace('/', '')

    return (
        <div className="companyInfo">
            <img src={company_logo} alt="company" className="companyInfo__logo"/>
            <div className="companyInfo__content">
                <h2>{company}</h2>
                <p>{companyUrlShort}</p>
            </div>
            <div className="companyInfo__action">
                <a href={company_url} className="button button--secondary">Company Site</a>
            </div>
        </div>
    )
}

export default CompanyInfo